<template>
  <div class="collect">
    <ArticlePage
      :list="articleList"
      title="我的收藏"
      :total="total"
      :current-page="query.page"
      @current-change="handleCurrentChange"
    />
  </div>
</template>
<script>
import ArticlePage from "@/components/contents/library/ArticlePage.vue";
import moment from 'moment'
import services from "@/services";
export default {
  name: "collect",

  components: {
    ArticlePage
  },

  data() {
    return {
      total: 0,
      query: {
        page: 1,
        pageSize: 10
      },

      articleList: []
    };
  },

  created() {
    this.getData();
  },

  methods: {
    getData() {
      services
        .getMyCollect(this.query)
        .then(res => {
          if (res.data.code !== 0) {
            return Promise.reject(res.data.msg);
          }
          this.articleList = res.data.data.records.map(item => {
            item.title = item.articleTitle;
            item.createTime = moment(item.createTime).format("YYYY-MM-DD HH:ss:mm");
            return item;
          });
        })
        .catch(err => {
          this.$msgbox({
            type: "error",
            message: err
          });
        });
    },

    handleCurrentChange() {
      this.getData();
    }
  }
};
</script>
<style lang="scss" scoped>
.wrap {
  padding: 24px 32px;
}

.tabs {
  height: 100% !important;
  overflow: auto;
}
</style>