<template>
  <div class="wrap article-width">
    <!-- <div class="header">
      <label>状态：</label>
      <el-select v-model="type" @change="handleTypeChange">
        <el-option
          v-for="(option) in applyOptions"
          :key="option.value"
          :label="option.label"
          :value="option.value"
        ></el-option>
      </el-select>
    </div>-->
    <section v-if="!isDetail">
      <div class="table">
        <el-table :data="tableData">
          <el-table-column type="index"></el-table-column>
          <el-table-column prop="userName" label="姓名" min-width="70px"></el-table-column>
          <el-table-column prop="phone" label="电话" min-width="100px"></el-table-column>
          <el-table-column prop="amount" label="亩数" min-width="80px"></el-table-column>
          <el-table-column prop="statusDesc" label="状态" min-width="100px"></el-table-column>
          <el-table-column prop="createTime" label="创建时间" min-width="170px"></el-table-column>
          <el-table-column label="操作" min-width="70px">
            <template slot-scope="scope">
              <el-button @click="handleDetail(scope.row)" type="text" size="small">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="clearfix" v-show="total > 0">
          <el-pagination
            class="pagenation right"
            background
            @current-change="handleCurrentChange"
            :current-page="query.page"
            :page-size="query.pageSize"
            layout="prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </section>

    <section v-else>
      <el-page-header @back="hiddleDetail" class="page-header" content="审核详情"></el-page-header>

      <el-form :model="detailData" label-width="80px" ref="form" size="small">
        <el-form-item prop="userName" label="姓名：">
          <el-input placeholder="请输入你的姓名" :disabled="true" v-model="detailData.name"></el-input>
        </el-form-item>
        <el-form-item prop="phone" label="电话：">
          <el-input placeholder="请输入你的电话" :disabled="true" v-model="detailData.phone"></el-input>
        </el-form-item>
        <el-form-item prop="address" label="地址：">
          <el-input placeholder="请输入你的地址" :disabled="true" v-model="detailData.address"></el-input>
        </el-form-item>
        <el-form-item prop="amount" label="亩数：">
          <el-input placeholder="请输入你的亩数" :disabled="true" v-model.number="detailData.amount"></el-input>
        </el-form-item>
        <el-form-item prop="content" label="照片：">
          <el-upload
            :action="getUploadUrl()"
            :with-credentials="true"
            list-type="picture-card"
            :file-list="detailData.content"
            :on-success="handleAvatarSuccess"
            :on-remove="handleRemove"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <div class="footer">
        <el-button type="warning" @click="handleReject">驳回</el-button>
        <el-button type="primary" @click="handlePass">通过审核</el-button>
      </div>
    </section>
  </div>
</template>
<script>
import services from "@/services";
export default {
  name: "ApprovalApply",
  data() {
    return {
      type: null,
      applyOptions: [],
      tableData: [],
      detailData: { content: [] },
      isDetail: false,
      total: 0,
      query: {
        page: 1,
        pageSize: 10
      }
    };
  },

  created() {
    this.getAdminApply();
  },

  methods: {
    handleTypeChange(val) {
      this.type = val;
    },

    hiddleDetail() {
      this.isDetail = false;
      this.detailData = { content: [] };
    },

    showError(err) {
      this.$msgbox({
        type: "error",
        message: err
      });
    },

    handleCurrentChange(val) {
      this.query.page = val;
      this.getAdminApply();
    },

    getUploadUrl() {
      return services.file.uploadUrl;
    },

    handleAvatarSuccess(res, file, fileList) {
      file.url = services.file.baseURL + res.data;
      this.detailData.content = fileList;
    },

    handleRemove(file) {
      let index = this.detailData.content.indexOf(file);
      this.detailData.content.splice(index, 1);
    },

    getAdminApply() {
      services
        .getAdminApply({ ...this.query })
        .then(res => {
          if (res.data.code !== 0) {
            return Promise.reject(res.data.msg);
          }
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
        })
        .catch(err => {
          this.showError(err);
        });
    },

    handleDetail(row) {
      this.isDetail = true;
      this.detailData = row;
      row.content =
        typeof row.content === "string" ? JSON.parse(row.content) : row.content;
      this.detailData.content = Array.isArray(row.content) ? row.content : [];
    },

    handleReject() {
      if (!this.detailData.content || this.detailData.content.length === 0) {
        this.$msgbox({
          type: "error",
          message: "请填写驳回理由",
          showCancelButton: true
        });
        return;
      }
      this.$msgbox({
        type: "warning",
        message: "确定驳回申请吗？",
        showCancelButton: true
      })
        .then(() => {
          let data = {
            applyId: this.detailData.id,
            status: 2,
            images: this.detailData.content.map(item => item.url)
          };
          services
            .handleApply(data)
            .then(res => {
              if (res.data.code !== 0) {
                return Promise.reject(res.data.code);
              }
              this.$msgbox({
                type: "success",
                message: "驳回成功"
              });
              this.hiddleDetail();
            })
            .catch(err => {
              this.showError(err);
            });
        })
        .catch(() => {
          return;
        });
    },

    handlePass() {
      this.$msgbox({
        type: "warning",
        message: "确定通过申请吗？",
        showCancelButton: true
      })
        .then(() => {
          let data = {
            applyId: this.detailData.id,
            status: 3,
            images: this.detailData.content.map(item => item.url)
          };
          services
            .handleApply(data)
            .then(res => {
              if (res.data.code !== 0) {
                return Promise.reject(res.data.code);
              }
              this.$msgbox({
                type: "success",
                message: "操作成功"
              });
              this.hiddleDetail();
            })
            .catch(err => {
              this.showError(err);
            });
        })
        .catch(() => {
          return;
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.wrap {
  padding: 24px 30px;
}
.page-header {
  border-bottom: 1px solid #eee;
  padding-bottom: 16px;
  margin-bottom: 20px;
}

.pagenation {
  margin-top: 20px;
}

.footer {
  text-align: center;
}
</style>