<template>
  <div class="wrap">
    <div class="clearfix row">
      <ArticleListPanel
        title="栽培方法"
        :opretionBtn="{path:'/zaipeiArticle',label:'更多'}"
        :list="zaipeiList"
        class="flex-row left"
      />
      <el-carousel height="268px" class="carousel left list-item">
        <el-carousel-item v-for="item in carouselList" :key="item">
          <p class="carousel-title">{{item.title}}</p>
          <img :src="item.coverImg" alt class="carousel-img" />
        </el-carousel-item>
      </el-carousel>
      <ArticleListPanel
        title="公告"
        :opretionBtn="{path:'/notice',label:'更多'}"
        :list="noticeList"
        class="flex-row right list-item"
      />
    </div>
    <div class="clearfix row">
      <ArticleListPanel
        title="病虫防治"
        :opretionBtn="{path:'/bingchongArticle',label:'更多'}"
        :list="bingchongList"
        class="flex-row left"
      />
    </div>
  </div>
</template>
<script>
import ArticleListPanel from "@/components/contents/ArticleListPanel/Index";
import services from "@/services";
export default {
  name: "Home",

  components: {
    ArticleListPanel
  },

  data() {
    return {
      zaipeiList: [],
      bingchongList: [],
      noticeList: [],
      carouselList: []
    };
  },

  created() {
    this.getzaipeiList();
    this.getCarouselList();
    this.getNotice();
    this.getbingchongList();
  },

  methods: {
    handleError(err) {
      console.error(err);
    },

    getNotice() {
      services
        .getNoticeArticleList({
          page: 1,
          pageSize: 6
        })
        .then(res => {
          if (res.data.code !== 0) {
            return Promise.reject(res.data.msg);
          }
          this.noticeList = res.data.data.records.map(item => {
            item.type = "notice";
            return item;
          });
        })
        .catch(err => {
          this.handleError(err);
        });
    },

    getzaipeiList() {
      services
        .getArticleList({
          page: 1,
          pageSize: 6,
          type: 3
        })
        .then(res => {
          if (res.data.code !== 0) {
            return Promise.reject(res.data.msg);
          }
          this.zaipeiList = res.data.data.records.map(item => {
            item.type = "zaipei";
            return item;
          });
        })
        .catch(err => {
          this.handleError(err);
        });
    },

    getbingchongList() {
      services
        .getArticleList({
          page: 1,
          pageSize: 6,
          type: 2
        })
        .then(res => {
          if (res.data.code !== 0) {
            return Promise.reject(res.data.msg);
          }
          this.bingchongList = res.data.data.records.map(item => {
            item.type = "bingchong";
            return item;
          });
        })
        .catch(err => {
          this.handleError(err);
        });
    },

    getCarouselList() {
      services
        .getSearchArticleList({
          page: 1,
          pageSize: 5,
          content: ""
        })
        .then(res => {
          if (res.data.code !== 0) {
            return Promise.reject(res.data.msg);
          }
          this.carouselList = res.data.data.records;
        })
        .catch(err => {
          this.handleError(err);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.wrap {
  border: 1px solid transparent;
}

.list-item {
  margin-left: 23px;
}

.row {
  margin-top: 30px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}

.flex-row {
  flex: none;
}

.carousel {
  width: 400px;
  flex: none;
  border: 1px solid rgba(0, 0, 0, 0.45);
  border-radius: 4px;
  position: relative;
}

.carousel-img {
  width: 400px;
}
.carousel-title {
  color: rgba(255, 255, 255, 1);
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  position: absolute;
  width: 100%;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>